import * as Util from '@antv/util';
import Element from './element';
import BBox from './bbox';
import * as Shapes from '../shapes/index';
import { ElementCFG } from '../interface';

const SHAPE_MAP = {}; // 
const INDEX = '_INDEX';

function getComparer(compare) {
  return function(left, right) {
    const result = compare(left, right);
    return result === 0 ? left[INDEX] - right[INDEX] : result;
  };
}

function find(children, x, y) {
  let rst;
  for (let i = children.length - 1; i >= 0; i--) {
    const child = children[i];
    if (child.cfg.visible && child.cfg.capture && !child.destroyed && !child.removed) {
      if (child.isGroup) {
        rst = child.getShape(x, y);
      } else if (child.isHit(x, y)) {
        rst = child;
      }
    }
    if (rst) {
      break;
    }
  }
  return rst;
}

class Group extends Element {
  isGroup = true;
  type = 'group';
  canFill = true;
  canStroke = true;

  constructor(cfg: ElementCFG = {}) {
    super(cfg);
    this.set('children', []);
    this.set('tobeRemoved', []);

    this._beforeRenderUI();
    this._renderUI();
    this._bindUI();
  }

  _beforeRenderUI() {}
  _renderUI() {}
  _bindUI() {}

  addShape(type, cfg) {
    const canvas = this.get('canvas');
    cfg = cfg || {};
    let shapeType = SHAPE_MAP[type];
    if (!shapeType) {
      shapeType = Util.upperFirst(type);
      SHAPE_MAP[type] = shapeType;
    }
    if (cfg.attrs && canvas) {
      const attrs = cfg.attrs;
      if (type === 'text') {
        // 
        const topFontFamily = canvas.get('fontFamily');
        if (topFontFamily) {
          attrs.fontFamily = attrs.fontFamily ? attrs.fontFamily : topFontFamily;
        }
      }
    }
    cfg.canvas = canvas;
    cfg.type = type;
    const rst = new Shapes[shapeType](cfg);
    this.add(rst);
    return rst;
  }

  /** 
   * @param  {Function|Object|undefined} param 
   * @param  {Object} cfg 
   * @return {Object} rst 
   */
  addGroup(param?: any, cfg?) {
    const canvas = this.get('canvas');
    let rst;
    cfg = Util.mix({}, cfg);
    if (Util.isFunction(param)) {
      if (cfg) {
        cfg.canvas = canvas;
        cfg.parent = this;
        rst = new param(cfg);
      } else {
        rst = new param({
          canvas,
          parent: this,
        });
      }
      this.add(rst);
    } else if (Util.isObject(param)) {
      // @ts-ignore
      param.canvas = canvas;
      rst = new Group(param);
      this.add(rst);
    } else if (param === undefined) {
      rst = new Group();
      this.add(rst);
    } else {
      return false;
    }
    return rst;
  }

  /** 
   * @param  {Array} padding 
   * @param  {Attrs} attrs 
   * @param  {Shape} backShape 
   * @return {Object} 
   */
  renderBack(padding, attrs) {
    let backShape = this.get('backShape');
    const innerBox = this.getBBox();
    // const parent = this.get('parent'); // getParent
    Util.mix(attrs, {
      x: innerBox.x - padding[3],
      y: innerBox.y - padding[0],
      width: innerBox.width + padding[1] + padding[3],
      height: innerBox.height + padding[0] + padding[2],
    });
    if (backShape) {
      backShape.attr(attrs);
    } else {
      backShape = this.addShape('rect', {
        zIndex: -1,
        attrs,
      });
    }
    this.set('backShape', backShape);
    this.sort();
    return backShape;
  }

  removeChild(item, destroy) {
    if (arguments.length >= 2) {
      if (this.contain(item)) {
        item.remove(destroy);
      }
    } else {
      if (arguments.length === 1) {
        if (Util.isBoolean(item)) {
          destroy = item;
        } else {
          if (this.contain(item)) {
            item.remove(true);
          }
          return this;
        }
      }
      if (arguments.length === 0) {
        destroy = true;
      }

      super.remove(destroy);
    }
    return this;
  }

  /**
   * shapegroup
   * @param {Object} items 
   * @return {Object} group 
   */
  add(items) {
    const self = this;
    const children = self.get('children');
    if (Util.isArray(items)) {
      Util.each(items, function(item) {
        const parent = item.get('parent');
        if (parent) {
          parent.removeChild(item, false);
        }
        self._setCfgProperty(item);
      });
      self.cfg.children = children.concat(items);
    } else {
      const item = items;
      const parent = item.get('parent');
      if (parent) {
        parent.removeChild(item, false);
      }
      self._setCfgProperty(item);
      children.push(item);
    }
    return self;
  }

  _setCfgProperty(item) {
    const cfg = this.cfg;
    item.set('parent', this);
    item.set('canvas', cfg.canvas);
    if (cfg.timeline) {
      item.set('timeline', cfg.timeline);
    }
  }

  contain(item) {
    const children = this.get('children');
    return children.indexOf(item) > -1;
  }

  getChildByIndex(index) {
    const children = this.get('children');
    return children[index];
  }

  getFirst() {
    return this.getChildByIndex(0);
  }

  getLast() {
    const lastIndex = this.get('children').length - 1;
    return this.getChildByIndex(lastIndex);
  }

  getBBox(): BBox {
    const self = this;
    let minX = Infinity;
    let maxX = -Infinity;
    let minY = Infinity;
    let maxY = -Infinity;
    const children = self.get('children');
    if (children.length > 0) {
      Util.each(children, (child: any) => {
        if (child.get('visible')) {
          /*if (child.isGroup && child.get('children').length === 0) {
            return;
          }*/
          const box = child.getBBox() as BBox;
          if (!box) {
            return true;
          }
          const tl = box.tl;
          const tr = box.tr;
          const bl = box.bl;
          const br = box.br;
          const leftTop = [tl.x, tl.y, 1];
          const leftBottom = [bl.x, bl.y, 1];
          const rightTop = [tr.x, tr.y, 1];
          const rightBottom = [br.x, br.y, 1];

          child.apply(leftTop);
          child.apply(leftBottom);
          child.apply(rightTop);
          child.apply(rightBottom);

          const boxMinX = Math.min(leftTop[0], leftBottom[0], rightTop[0], rightBottom[0]);
          const boxMaxX = Math.max(leftTop[0], leftBottom[0], rightTop[0], rightBottom[0]);
          const boxMinY = Math.min(leftTop[1], leftBottom[1], rightTop[1], rightBottom[1]);
          const boxMaxY = Math.max(leftTop[1], leftBottom[1], rightTop[1], rightBottom[1]);

          if (boxMinX < minX) {
            minX = boxMinX;
          }

          if (boxMaxX > maxX) {
            maxX = boxMaxX;
          }

          if (boxMinY < minY) {
            minY = boxMinY;
          }

          if (boxMaxY > maxY) {
            maxY = boxMaxY;
          }
        }
      });
    } else {
      minX = 0;
      maxX = 0;
      minY = 0;
      maxY = 0;
    }
    return new BBox(minX, minY, maxX - minX, maxY - minY);
  }

  getCount() {
    return this.get('children').length;
  }

  sort() {
    const children = this.get('children');
    // 
    Util.each(children, (child, index) => {
      child[INDEX] = index;
      return child;
    });

    children.sort(
      getComparer(function(obj1, obj2) {
        return obj1.get('zIndex') - obj2.get('zIndex');
      })
    );

    return this;
  }

  findById(id) {
    return this.find(function(item) {
      return item.get('id') === id;
    });
  }

  /**
   * 
   * @param  {Function} fn 
   * @return {Canvas.Base} 
   */
  find(fn) {
    if (Util.isString(fn)) {
      return this.findById(fn);
    }
    const children = this.get('children');
    let rst = null;

    Util.each(children, (item: any) => {
      if (fn(item)) {
        rst = item;
      } else if (item.find) {
        rst = item.find(fn);
      }
      if (rst) {
        return false;
      }
    });
    return rst;
  }

  /**
   * @param  {Function} fn filter mathod
   * @return {Array} all the matching shapes and groups
   */
  findAll(fn) {
    const children = this.get('children');
    let rst = [];
    let childRst = [];
    Util.each(children, (item: any) => {
      if (fn(item)) {
        rst.push(item);
      }
      if (item.findAllBy) {
        childRst = item.findAllBy(fn);
        rst = rst.concat(childRst);
      }
    });
    return rst;
  }

  /**
   * @Deprecated
   * @param  {Function} fn filter method
   * @return {Object} found shape or group
   */
  findBy(fn) {
    const children = this.get('children');
    let rst = null;

    Util.each(children, (item: any) => {
      if (fn(item)) {
        rst = item;
      } else if (item.findBy) {
        rst = item.findBy(fn);
      }
      if (rst) {
        return false;
      }
    });
    return rst;
  }

  /**
   * @Deprecated
   * @param  {Function} fn filter mathod
   * @return {Array} all the matching shapes and groups
   */
  findAllBy(fn) {
    const children = this.get('children');
    let rst = [];
    let childRst = [];
    Util.each(children, (item: any) => {
      if (fn(item)) {
        rst.push(item);
      }
      if (item.findAllBy) {
        childRst = item.findAllBy(fn);
        rst = rst.concat(childRst);
      }
    });
    return rst;
  }

  getShape(x, y, e?) {
    const self = this;
    const clip = self.attrs.clip;
    const children = self.cfg.children;
    let rst;
    if (clip) {
      const v = [x, y, 1];
      clip.invert(v, self.get('canvas')); // 
      if (clip.isPointInPath(v[0], v[1])) {
        rst = find(children, x, y);
      }
    } else {
      rst = find(children, x, y);
    }
    return rst;
  }

  clearTotalMatrix() {
    const m = this.get('totalMatrix');
    if (m) {
      this.setSilent('totalMatrix', null);
      const children = this.cfg.children;
      for (let i = 0; i < children.length; i++) {
        const child = children[i];
        child.clearTotalMatrix();
      }
    }
  }

  clear(delayRemove?) {
    if (this.destroyed) {
      return;
    }
    const children = this.cfg.children;
    for (let i = children.length - 1; i >= 0; i--) {
      children[i].remove(true, delayRemove);
    }
    this.cfg.children = [];
    return this;
  }

  destroy() {
    if (this.destroyed) {
      return;
    }
    this.clear();
    super.destroy();
  }

  clone() {
    const self = this;
    const children = self.cfg.children;
    const clone = new Group();
    Util.each(children, (child) => {
      clone.add(child.clone());
    });
    return clone;
  }
}

export default Group;
