import { Option } from '@antv/g2';
import { ScaleConfig } from '@antv/scale';
import { AttributeCfg, LabelOptions } from '@antv/g2/lib/element/base';
import { AdjustCfg } from '@antv/g2/lib/interface';
import { LooseMap } from './types';

export interface ITitle {
  visible: boolean;
  text: string;
  style?: {};
  alignWithAxis?: boolean;
}

export interface IDescription {
  visible: boolean;
  text: string;
  style?: {};
  alignWithAxis?: boolean;
}

type IEvents = LooseMap<string>;

export type Formatter = (text: string, item: any, idx: number) => string;

// TODO: g style
export interface IStyleConfig {
  [key: string]: string | number | boolean;
}

export interface IBaseAxis {
  /** true */
  visible?: boolean;
  /** scale */
  type?: 'linear' | 'time' | 'cat' | 'dateTime' | 'category' | 'log' | 'pow' | 'timeCat';
  /** scale  nice */
  nice?: boolean;
  /** scale  tickMethod */
  tickMethod?: string | ((cfg: ScaleConfig) => number[]);
  /**  */
  line?: {
    visible?: boolean;
    style?: IStyleConfig;
  };
  grid?: {
    /**  */
    visible?: boolean;
    style?: IStyleConfig | ((text: string, idx: number, count: number) => IStyleConfig);
  };
  /**  */
  gridType?: 'line' | 'arc';
  /**  */
  gridAlternateColor?: string | string[];
  autoRotateLabel?: boolean | number[]; //  label  true
  autoHideLabel?: boolean; //  label  false
  autoRotateTitle?: boolean;
  label?: {
    visible?: boolean;
    formatter?: (...args: any[]) => string;
    offset?: number; // 
    offsetX?: number; //  offset  x 
    offsetY?: number; //  offset  y 
    rotate?: number; // label 
    useHtml?: boolean; //  HTML 
    htmlTemplate?: string; //  label  html  useHtml: true 
    style?: IStyleConfig;
  };
  title?: {
    visible?: boolean;
    autoRotate?: boolean;
    text?: string;
    offset?: number;
    style?: IStyleConfig;
  };
  tickLine?: {
    visible?: boolean;
    style?: IStyleConfig;
  };
  events?: IEvents;
}
/** Linear */
export interface IValueAxis extends IBaseAxis {
  type?: 'linear';
  /** tick */
  min?: number;
  max?: number;
  minLimit?: number;
  maxLimit?: number;
  tickCount?: number;
  tickInterval?: number;
}
/**  */
export interface ITimeAxis extends IBaseAxis {
  type?: 'time';
  /** tick */
  tickInterval?: string;
  tickCount?: number;
  groupBy?: string;
}
/**  */
export interface ICatAxis extends IBaseAxis {
  type?: 'cat';
  /** tick */
  tickInterval?: number;
  tickCount?: number;
  groupBy?: string;
}

export type Axis = ICatAxis | IValueAxis | ITimeAxis;

export interface Label {
  visible: boolean;
  type?: string;
  formatter?: (text: string, item: any, idx: number, ...extras: any[]) => string;
  /**  label  */
  precision?: number;
  /**  */
  suffix?: string;
  style?: {};
  offset?: number;
  offsetX?: number;
  offsetY?: number;
  events?: IEvents;
  position?: string;
  adjustColor?: boolean;
  adjustPosition?: boolean;
  autoRotate?: boolean;
  labelLine?: any;
}

export interface Legend {
  visible?: boolean;
  /**  */
  position?: string;
  /**  */
  flipPage?: boolean;
  events?: IEvents;
  formatter?: (...args: any) => string;
  offsetX?: number;
  offsetY?: number;
  clickable?: boolean;
}

export interface Tooltip {
  visible: boolean;
  shared: boolean;
  /** html */
  showTitle?: boolean;
  html?: HTMLDivElement;
  formatter?: (...args: any) => string;
  htmlContent?: (title: string, items: any[]) => string;
  containerTpl?: string;
  itemTpl?: string;
  /**  */
  crosshair?: 'x' | 'y' | 'cross' | boolean;
  crosshairs?: { type: string; style?: IStyleConfig }; // FIXME:
  style?: IStyleConfig;
}

interface Animation {
  /** or */
  type?: string;
  duration?: number;
  easing?: string;
}

// tslint:disable-next-line: no-empty-interface
export interface Theme {}

export interface ElementOption {
  type: string;
  position: {
    fields: string[];
  };
  color?: AttributeCfg;
  size?: AttributeCfg;
  shape?: AttributeCfg;
  style?: IStyleConfig;
  label?: LabelOptions | false;
  animate?: {};
  adjust?: AdjustCfg[];
  connectNulls?: boolean;
  widthRatio?: {
    [type: string]: number;
  };
}

export interface G2Config {
  scales: Option.ScalesOption;
  legends: Option.LegendOption | Option.LegendsOption | boolean;
  tooltip: Option.TooltipOption | boolean;
  axes: Option.AxesOption | boolean;
  coord: Option.CoordinateOption;
  element?: ElementOption;
  elements: ElementOption[];
  annotations: any[];
  interactions: {};
  theme: any;
  panelRange: any;
  animate: any;
}

export interface IColorConfig {
  fields?: string[];
  values?: string[];
  callback?: (...args: any[]) => any;
}

const SECOND = 1000;
const MINUTE = 60 * SECOND;
const HOUR = 60 * MINUTE;
const DAY = 24 * HOUR;
const WEEK = DAY * 7;
const MONTH = DAY * 30;
const YEAR = DAY * 365;
export const timeIntervals = {
  second: { value: SECOND, format: 'HH:mm:ss' },
  miniute: { value: MINUTE, format: 'HH:mm' },
  hour: { value: HOUR, format: 'HH' },
  day: { value: DAY, format: 'YYYY-MM-DD' },
  week: { value: WEEK, format: 'YYYY-MM-DD' },
  month: { value: MONTH, format: 'YYYY-MM' },
  year: { value: YEAR, format: 'YYYY' },
};

interface StateCondition {
  name: string;
  exp: () => boolean | string | number;
}

export interface StateConfig {
  condition: () => any | StateCondition;
  style?: IStyleConfig;
  related?: string[];
}

export interface SliderConfig {
  visible?: boolean;
  start?: number;
  end?: number;
  foregroundColor?: string;
  backgroundColor?: string;
  height?: number;
  paddingTop?: number;
  paddingBottom?: number;
}

export interface ISliderInteractionConfig {
  /**  horizontal */
  type?: 'horizontal' | 'vertical';
  /**  vertical  */
  width?: number;
  /**  horizontal  */
  height?: number;
  /**  padding */
  padding?: [number, number, number, number];
  /**   */
  foregroundColor?: string;
  /**  */
  backgroundColor?: string;
  /** , 0~1 */
  start?: number;
  /** , 0~1 */
  end?: number;
}

export interface IScrollBarInteractionConfig {
  /**  horizontal */
  type?: 'horizontal' | 'vertical';
  /**  vertical  */
  width?: number;
  /**  horizontal  */
  height?: number;
  /**  padding */
  padding?: [number, number, number, number];
  /** XX */
  categorySize?: number;
}

export type IInteractionConfig = IScrollBarInteractionConfig | ISliderInteractionConfig;

export interface IInteractions {
  type: string;
  cfg?: IInteractionConfig;
}

/**
 * 
 */
export interface Point {
  readonly x: number;
  readonly y: number;
}

export interface DataItem {
  [field: string]: string | number | number[] | null | undefined;
}
