import { Interaction } from '@antv/g2';

export default class LineSelect extends Interaction {
  constructor(cfg) {
    super({
      endEvent: 'click',
      ...cfg,
    });
  }

  public end(ev) {
    const target = ev.target;
    if (target.name === 'line') {
      const data = ev.data[0]._origin;
      this.view.setInactive((obj) => {
        return obj !== data;
      });
      // TODO z-index
    } else {
      this.view.setInactive(() => {
        return false;
      });
      // TODO: z-index
    }
  }
}
